* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

.weather {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.weather-content-now {
  width: 100%;
  height: 100%;
  text-align: center;
  color: white;
  font-size: 18px;
  font-weight: 600;
}
.title-now {
  font-size: 38px;
}
.weather-week {
  width: 100%;
  display: flex;
  color: white;
  flex-direction: column;
  gap: 30px;
  margin-bottom: 50px;
  padding-left: 10px;
  padding-right: 10px;
}
.weather-week-title {
  width: 100%;
  text-align: center;
  font-size: 20px;
  color: white;
}
.weather-details {
  display: flex;
  width: 100%;
  justify-content: space-between;
  font-size: 18px;
  font-weight: 600;
}

#root {
  display: flex;
  background-image: url(https://i.pinimg.com/originals/39/62/7b/39627ba970f978b8756c7c1d2f591f62.jpg);
  background-size: cover;
  color: white;
  height: 100vh;
  justify-content: center;
}
.App {
  display: flex;
  flex-direction: column;
  gap: 20px;
  width: 100%;
  margin: 0px;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
}

.coordinate-form {
  margin-bottom: 60px;
  position: relative;
  width: 300px;
  margin: 0 auto;
}

.coordinate-input {
  color: white;
  font-size: 20px;
  background: transparent;
  box-sizing: border-box;
  border: 1px solid white;
  height: 35px;
  padding: 15px;
  padding-right: 40px;
  outline: none;
}
.coordinate-input::placeholder {
  color: rgba(48, 99, 144, 0.6);
  font-size: 15px;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
}

.weather-left-form {
  display: flex;
  flex-direction: column;
  gap: 20px;

  justify-content: center;
  align-items: center;
}
.weather-right-form {
  display: flex;
  flex-direction: column;
  font-size: 30px;
}

.weather-big-container {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 50px;
  margin-top: 30px;
}
.btn-weather-coordinates {
  width: 140px;
  padding: 15px, 17px;
  color: white;
  font-size: 20px;
  background: transparent;
  border: 2px solid white;
  cursor: pointer;
}
.weather-header-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  justify-content: center;
  margin-top: 30px;

  text-align: center;
}
.nameCity,
.date {
  font-size: 25px;
  color: rgba(8, 81, 145);
}
